    };
    const imageUrl1 = [Home1];
    const imageUrl2 = [Home2];
    const image = [png];
    const noticeMessages = ref("");
    const show = ref(false);
    const show_1 = ref(false);
    const show_2 = ref(true); // åˆå§‹æ—¶æ˜¾ç¤ºåŠ è½½ä¸­æ•ˆæžœ
    const router = useRouter();
    const active = ref(2);
    const images = [jpg1, jpg2, jpg3];
    const userList = ref([]);
    // ä½¿ç”¨ onMounted é’©å­æ¥æ¨¡æ‹Ÿ created é’©å­ï¼Œå‘èµ· API è¯·æ±‚
    onMounted(() => {
      fetchLoginToken();
      fetchData();
    });
    const token = localStorage.getItem("jwtToken"); // ä»ŽlocalStorageèŽ·å–JWTä»¤ç‰Œ
    if (!token) {
      router.replace("/login");
    }
    const headers = {
