    };
    let userId = "";
    const fetchLoginToken = () => {
      axios.post("/api/loginToken?token=" + token).then(response => {
        if (response.data.code == 0) {
          console.error(response.data.data);
          router.replace("/login");
          return;
        }
        console.log(response.data.data);
        const decodedToken = jwtDecode(token);
        // ä»Žè§£ç åŽçš„ä»¤ç‰Œä¸­èŽ·å–ç‰¹å®šçš„æ•°æ®
        userId = decodedToken.id; // ä»Žä»¤ç‰Œä¸­èŽ·å–ç”¨æˆ·ID
        // åœ¨è¿™é‡Œå¤„ç†ç™»å½•ä»¤ç‰ŒæŽ¥å£çš„å“åº”
        // å¦‚æžœéœ€è¦æ‰§è¡Œä¸€äº›ç‰¹å®šçš„æ“ä½œï¼Œå¯ä»¥åœ¨è¿™é‡Œæ·»åŠ ä»£ç 
      }).catch(error => {
        // åˆ é™¤æœ¬åœ°å­˜å‚¨çš„ token
        localStorage.removeItem("jwtToken");
        router.replace("/login");
        console.error("è¯·æ±‚loginTokenæŽ¥å£å¤±è´¥", error);
      });
    };
    const fetchData = () => {
      // ä½¿ç”¨ axios æˆ– fetch å‘èµ· API è¯·æ±‚ï¼ŒèŽ·å–å•†å“æ•°æ®
      // å‡è®¾ API è·¯å¾„ä¸º '/api/products'
      // åœ¨å‘èµ·æ•°æ®è¯·æ±‚ä¹‹å‰
      console.log("Before fetching data. userList:", userList.value);
      axios.get("/api/user", {
